@import '../../../../styles/variables';

.background-selector {
    position: fixed;
    bottom: $footer-height;
    right: 0;
    z-index: 1;

    &__visible-button {
        padding: 0.2rem 1rem;
        float: right;
        color: white;
        background-color: #AAAADDDD;
        border: 1px solid #83838388;
        font-size: 1.2rem;
        border-radius: 5px 5px 0 0;
        cursor: pointer;
    }

    &__options-container {
        width: 100%;
    }

    &__options {
        width: 100%;
        padding-left: 0.2rem;
        padding-right: 0.2rem;
        color: white;
        background-color: #888888DD;
        font-size: 1.2rem;
        cursor: pointer;
    }
}